import styled from "../../theme";
import { H2 } from "src/theme/elements/Headings";
import { Button } from "src/theme/objects/Buttons";
import { hoverOpacity } from "src/theme/utils/hovers";

export const Wrapper = styled.div`
	min-height: ${p => p.theme.spacing.default(5)};
	background-color: #fff;
	box-shadow: ${p => p.theme.shadows.box};
	z-index: 1;
	display: flex;
	align-items: center;
	padding-left: ${p => p.theme.spacing.default(2)};
	padding-right: ${p => p.theme.spacing.default(2)};
	justify-content: space-between;
`;

export const Logo = styled(H2)`
	color: ${p => p.theme.colors.primary()};
	margin-bottom: unset;
	font-family: ${p => p.theme.fonts.fontLogo};
	font-size: 21px;

	${hoverOpacity}
`;

export const Logout = styled(Button)`
	margin-top: ${p => p.theme.spacing.default(1.5)};
`;

export const WrapperUser = styled.div`
	display: flex;
	align-items: center;
	position: relative;
`;

export const Avatar = styled.img`
	border-radius: 50px;
	width: ${p => p.theme.spacing.default(2)};
	margin-left: ${p => p.theme.spacing.default(1.5)};

	${hoverOpacity}
`;



