import * as React from "react";
import { ShoppingList } from "src/models/ShoppingListModel";
import {
	ShoppingListBox,
	LinkTitle,
	WrapperNameRemove,
	WrapperShortProducts,
	ProductShort,
	WrapperInput,
	WrapperShare
} from "./shoppingStyled";
import { RouteProps } from "react-router-dom";
import routes from "src/routes";
import { DateComponent } from "./DateComponent";
import { Product } from "src/models/ProductModel";
import { I } from "src/theme/objects/Icons";
import useOnClickOutside from "use-onclickoutside";
import { WrapperPopup, Popup } from "src/theme/objects/Popups";
import { InputComponent } from "../forms/InputComponent";
import { Button } from "src/theme/objects/Buttons";
import { Form } from "src/theme/objects/Forms";
import { Message, MessageType } from "src/models/messageModel";

interface Props {
	shoppingList: ShoppingList[];
	removeList: (uniqueId: string) => any;
	addToSharingList: (uniqueId: string, ownerEmail: string) => any;
	addMessage: (message: Message) => any;
}

export const ShoppingListComponent = (props: Props & RouteProps) => {
	const EMAIL_REGEX = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
	const [emailError, setEmailError] = React.useState<string>("");
	const [togglePopup, setTogglePopup] = React.useState<string>("");
	const [ownerEmail, setOwnerEmail] = React.useState<string>("");

	const popupElement = React.useRef(null);
	useOnClickOutside(popupElement, () => {
		setTogglePopup("");
		setEmailError("");
		setOwnerEmail("");
	});

	const renderProducts = (products: Product[]) => {
		return products.map(p => {
			return (
				<ProductShort
					checked={p.checked ? p.checked : false}
					key={Math.random()}
				>
					{p.name}
				</ProductShort>
			);
		});
	};

	const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		if (!EMAIL_REGEX.test(e.target.value)) {
			return setEmailError("Incorect email");
		} else {
			setEmailError("");
			setOwnerEmail(e.target.value);
		}
	};

	const onSubmit = (e: React.FormEvent<HTMLFormElement>, uniqueId: string) => {
		e.preventDefault();
		if (emailError || !ownerEmail) {
			return;
		}

		props.addToSharingList(uniqueId, ownerEmail);
		setTogglePopup("");
	};

	const renderPopup = (uniqueId: string, i: number) => {
		return (
			<WrapperPopup show={true}>
				<Popup ref={popupElement}>
					<WrapperInput>
						<Form
							onSubmit={(e: React.FormEvent<HTMLFormElement>) =>
								onSubmit(e, uniqueId)
							}
						>
							<InputComponent
								focused={true}
								fieldType="text"
								name="ownerEmail"
								label="Share shopping list"
								onChange={onChange}
								placeholder="name@gmail.com"
								activePlaceholder={true}
								errorMessage={emailError}
							/>
							<Button small={true} variant={"success"}>
								Share list
							</Button>
						</Form>
					</WrapperInput>
				</Popup>
			</WrapperPopup>
		);
	};
	const renderShoppingList = () => {
		return props.shoppingList.map((sl, i) => (
			<ShoppingListBox color={sl.color} key={sl.uniqueId}>
				<I onClick={() => props.removeList(sl.uniqueId)} top={10} right={10}>
					delete
				</I>
				<WrapperShare>
					<I onClick={() => setTogglePopup(sl.uniqueId)}>share</I>
					{togglePopup === sl.uniqueId && renderPopup(sl.uniqueId, i)}
				</WrapperShare>
				<DateComponent date={sl.createdAt} users={sl.users} />
				<WrapperNameRemove>
					<LinkTitle to={routes.shoppingListTemplate(sl.uniqueId)}>
						{sl.name ? sl.name : "Change name list"}
					</LinkTitle>
				</WrapperNameRemove>
				<WrapperShortProducts>
					{renderProducts(sl.products)}
				</WrapperShortProducts>
			</ShoppingListBox>
		));
	};

	return <>{renderShoppingList()}</>;
};



