import * as React from "react";
import { WrapperSummary, Sum, Currency } from "./shoppingStyled";

interface Props {
	sum: number;
}

export const SummaryComponent = (props: Props) => {
	return (
		<WrapperSummary>
			Sum:
			<Sum>{props.sum}</Sum>
			<Currency>zł</Currency>
		</WrapperSummary>
	);
};



