import * as React from "react";
import * as ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { GlobalStyle } from "./theme/elements/Global";
import { themeProps, ThemeProvider } from "./theme";
import {
	HashRouter as Router,
	Route,
	Switch,
	Redirect
} from "react-router-dom";
import routes from "./routes";
import LoginComponent from "./components/login/LoginComponent";
import configureStore from "./store";
import AuthComponent from "./components/auth/AuthComponent";
import ProtectedAreaComponent from "./components/auth/ProtectedAreaComponent";
import MessageComponent from "./components/messages/MessageComponent";
import SpinnerComponent from "./components/spinner/SpinnerComponent";

ReactDOM.render(
	<Provider store={configureStore()}>
		<GlobalStyle theme={themeProps} />
		<ThemeProvider theme={themeProps}>
			<Router>
				<React.Fragment>
					<SpinnerComponent />
					<MessageComponent />
					<Route
						exact={true}
						path={routes.main}
						render={() => <Redirect to={routes.login} />}
					/>
					<Switch>
						<Route path={routes.login} component={LoginComponent} />
						<AuthComponent
							path={routes.auth}
							component={ProtectedAreaComponent}
						/>
					</Switch>
				</React.Fragment>
			</Router>
		</ThemeProvider>
	</Provider>,
	document.getElementById("root") as HTMLElement
);



